.cr{
    width: 100%;
    height:650px ;
    background-color: pink;
    position: relative;
}
h3{
    font-size:30px;
    font-weight: 1000;
    display: flex;
    justify-content: center;
    position: absolute; 
    margin-left: 50%; 
}

.para{
    width: 300px;
    height: 500px;
    background-color: rgb(224, 202, 202);
    border: 5px solid rgb(26, 17, 2);
    border-radius: 10px;
    display: inline-block;
    margin: 20px;
    margin-top: 100px;
    margin-left: 40px;
    padding: 10px;
}
.para img{
    width: 100%;
    height: auto;
    margin: 1px;
}
.para .f p{
    font-family: cursive;
    text-align: center;

}
a{
    text-decoration: none;
    color: rgb(253, 255, 254);
}
button{
    margin-top:30px;
    margin-bottom: 30px;
    margin-left:115px;
    padding: 10px;
    border: 2px solid rgb(221, 131, 57);
    background-color: rgb(27, 27, 27);
    border-radius: 10px;
    cursor: pointer;
}
button a:hover{
    color:tomato ;
}
button:hover{
    color:blanchedalmond;
}
