nav a{
    text-decoration: none;
    color: aquamarine;
    text-align: center;
    padding: 20px;
    float: left;
    font-weight: 600;
    
}
nav{
  background-color: black;
  padding: 5px;
  overflow: hidden;
}
span a{
  color: tomato;
  float: right;
}
nav a:hover{
  color: tomato;
  transition: 0.5s;
}
span a:hover{
  color: aquamarine;
  transition: 1.0s;
}