.bg{
    width: 100%;
    height: 500px;
    background-image: url("dK.jpg");
    background-position: cover;
    background-repeat: no-repeat;
    position: relative;
}
.bg img{
    width: 100%;
    height: auto;

}

h2{
    color: aliceblue;
    display: flex;
    justify-content: center;
    position: absolute;
    margin-left: 40%; 
    margin-top: 200px;
    font-size: 50px;

}
h2:hover{
    color: crimson;
    transition: 0.3s ease;
}